.addreport {
    text-align: center;
}

.thumbnail {
    border: 2px solid #ced4da;
    border-radius: .9rem;
    padding: 5px;
    margin-top: 20px;
    max-width: 70%;
    max-height: 10%;
}

.title {
    justify-content: center!important;
    display: flex!important;
}

.sub-title-button {
    justify-content: center!important;
    display: flex!important;
}


.footer-link {
    color: black;
}

.footer-link:hover {
    color: black;
}

.footer-link:link {
    color: black;
}

.footer-link:active {
    color: black;
}

.footer-link:visited {
    color: black;
}

.card-list-sub {
    align-items: center;
    text-align: center;
    margin: 20px auto;
    border: 2px rgba(0,0,0,0.2) solid;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 85%;
    border-radius: 10px;
    background-color: white;
  }

.card-list-sub:hover {
    box-shadow: 0 8px 20px 0 rgba(0,0,0,0.2);
}

.card-container {
    margin-top: 10px;
    padding: 4px 16px;
    font-size: 20px;
}

.card-img {
    width: 30vh;
    padding: .1em;
    margin-top: 15px;
    border-radius: 10px;
    border: solid 3px gray;
    border-style: dashed;
}

.text-gray {
    font-size: 18px;
    --tw-text-opacity: 1;
    color: rgba(156,163,175,var(--tw-text-opacity));
}

.summitform {
    transition: all 1s ease-in;
}

.imagedes {
    text-align: center;
}


.input-file-button {
    border: 1px solid #ced4da;
    width: 100%;
    border-radius: .25rem;
    padding: .375rem .75rem;
    vertical-align: middle;
    background-color: white;
}

.subtitle-map {
    text-decoration-line: none;
}


@font-face {
    font-family: 'Cafe24Oneprettynight';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_twelve@1.1/Cafe24Oneprettynight.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

* {
 font-family: 'Cafe24Oneprettynight';
}